import React from "react";
import Header from "../../components/Header/Header";
import Footer from "../../components/Footer/Footer";
import './contacts.css';
import ContactForm from "./contactForm";

function ContactPage() {
    React.useEffect(() => {
        window.scrollTo(0, 0);
    }, []);
    return (
            <div className="app">
                <Header logo='img/logo.svg' phone='' button=''></Header>
                <main className='main-contact'>
                    <div className="section">
                        <div className="container">
                            <h1 className="page-title">Работа твоей мечты</h1>
                            <div className="d-flex j-between contact-flex">
                                <div className="col">
                                    <ContactForm></ContactForm>
                                </div>
                                <div className="col">
                                    <h2 className="col-title">Наша суперцель</h2>
                                    <p>— стать любимым магазином для каждой российской семьи.</p>
                                    <p>Сотни тысяч наших сотрудников ежедневно работают над её достижением.</p>
                                    <p>Мы уверены, что в ближайшие годы достигнем этого и будет здорово,если вместе с тобой.</p>
                                    <a href="tel:79264331416">+7 (926) 433-14-16</a>
                                </div>
                            </div>
                        </div>
                    </div>
                </main>     
                <Footer logo='img/logo.svg'></Footer>           
            </div>

    );
}

export default ContactPage;