import React from "react";
import parse from "html-react-parser";
function Slide({slide}) {
    
    return (
            <div className="item" id={slide.id}>
                <div className="d-flex">
                    <div className="col">
                        <div className="title">{parse(slide.title)}</div>
                    </div>
                    <div className="col">
                        <div className="thumb"><img src={slide.image} alt={slide.title} /></div>
                        <div className="text text-1">{slide.text1}</div>
                        <div className="text text-2">{slide.text2}</div>
                    </div>
                </div>
            </div>
    );
}

export default Slide;