import React, {useCallback, useEffect, useState} from 'react'
import { GoogleMap, Marker, useJsApiLoader } from '@react-google-maps/api';
import markerIcon from '../../img/map-marker.png';

const containerStyle = {
  width: '100%',
  height: '100%'
};


const markers = [{
  id: 0,
  lat: 50.542246, 
  lng: 30.405241,
  type: 2
},
{
  id: 1,
  lat: 50.526885, 
  lng: 30.513391,
  type: 1
},
{
  id: 2,
  lat: 50.536063,
  lng: 30.719793,
  type: 2
},
{
  id: 3,
  lat: 50.482852,
  lng: 30.572575,
  type: 1
},
{
  id: 4,
  lat: 50.454554, 
  lng: 30.372795,
  type: 2
},
{
  id: 5,
  lat: 50.431756, 
  lng: 30.520032,
  type: 1
},
{
  id: 6,
  lat: 50.436537, 
  lng: 30.703644,
  type: 2
},
{
  id: 7,
  lat: 50.421088, 
  lng: 30.765426,
  type: 1
},
{
  id: 8,
  lat: 50.408211, 
  lng: 30.651101,
  type: 2
},
{
  id: 9,
  lat: 50.416674, 
  lng: 30.529847,
  type: 1
},
{
  id: 10,
  lat: 50.410419,
  lng: 30.452476,
  type: 2
},
{
  id: 11,
  lat: 50.387967, 
  lng: 30.472108,
  type: 1
},
{
  id: 12,
  lat: 50.365259, 
  lng: 30.618320,
  type: 2
}
];

const MapStyles = [
  {
    featureType: "all",
    elementType: "all",
    stylers: [
      { saturation: -100 } // <-- THIS
    ]
  }
];

const MyComponent = (props) => {
  const { isLoaded } = useJsApiLoader({
    id: 'google-map-script',
    googleMapsApiKey: "AIzaSyADUETl-k1ZlvYm862Q9pW7UoUyix2bWMw"
  })

  const [map, setMap] = React.useState(null)
  const [cordinates , setCordinates] = useState(markers);

  const onLoad = useCallback((map) => setMap(map), []);

  useEffect((props) => {
    if (map) {
      const bounds = new window.google.maps.LatLngBounds();
      markers.map(marker => (
        bounds.extend({
          lat: marker.lat,
          lng: marker.lng,
        })
      ))
      map.fitBounds(bounds);
    }
  }, [map]);

  function handleClick(e) {
    e.preventDefault();
    const mtype = e.target.getAttribute("datatype");
    document.querySelector('.map-nav .btn.active').classList.remove('active');
    e.target.classList.add('active')
    hideMarkers(markers, Number(mtype))
  }

  function hideMarkers(markers, type) {
    var newMarkers = [];
    if(type > 0){
      for (let marker of markers){
        if(marker.type === type){
          newMarkers.push(marker);
        }
      } 
    }else{
      newMarkers = markers;
    }
    setCordinates(newMarkers);
  }

  return isLoaded ? (
    <div className="map-wrap">
      <div id="map">
        <GoogleMap
          mapContainerStyle={containerStyle}
          defaultCenter={markers[0]}
          defaultZoom={11}
          onLoad={onLoad}
          options={{
            styles: MapStyles,
            mapTypeControl: false,
            scaleControl: false,
            streetViewControl: false,
            overviewMapControl: false,
            fullscreenControl:false,
            panControl: false,
        }}
        >
          {cordinates.map(marker => (
            <Marker
                key={marker.id}
                position={{ lat: marker.lat, lng: marker.lng }}
                icon={markerIcon}
              />
            ))}
        </GoogleMap>
      </div>
      <div className="map-nav">
          <button type="button" className="btn btn-fill white" datatype="1" onClick={handleClick}>юрлица</button>
          <button type="button" className="btn btn-fill white" datatype="2" onClick={handleClick}>физлица</button>
          <button type="button" className="btn btn-fill white active" datatype="0" onClick={handleClick}>показать всё</button>
      </div>
    </div>
  ) : <></>
}

export default React.memo(MyComponent)