import React, { useState }  from 'react';
import './header.css';
import { Link, useLocation } from "react-router-dom";
import {ReactComponent as Times} from '../../img/times.svg';
import {ReactComponent as Phone} from '../../img/phone.svg';


const Header = (props) => {
  
  var str = 'tel:' + props.phone.replace(/\D/g,'');
  const location = useLocation();
  const [headerClassName , setHeaderClassName] = useState('header');

  const handleScroll = (headerClassName) => {
      if (headerClassName !== 'header scrolled' && window.pageYOffset >= 100) {
          setHeaderClassName('header scrolled');
      } else if (headerClassName === 'header scrolled' && window.pageYOffset < 100) {
          setHeaderClassName('header');
      }
  }

  React.useEffect(() => {
      // window.scrollTo(0, 0);
      window.onscroll = () => handleScroll(headerClassName);
  }, [headerClassName]); // IMPORTANT, This will cause react to update depending on change of this value

  if(location.pathname === '/contact' || location.pathname === '/thankyou'){
    return (
            <header className='header secondary' id='headerEl'>
                <div className='container'>
                    <div className='d-flex j-between a-center'>
                      <div className='header-brand'>
                        <Link to="/">
                          <img 
                            src={props.logo}
                            alt='Gros'
                          />
                        </Link> 
                      </div>
                      <div className='header-action'>
                        <Link to="/" className="return-btn">
                          <Times></Times>
                        </Link>
                      </div>
                    </div>
                </div>
            </header>
    );      
    }else{
     
    return (
            <header className={headerClassName} id='headerEl'>
                <div className='container'>
                    <div className='d-flex j-between a-center'>
                      <div className='header-brand'>
                        <Link to="/">
                          <img 
                            src={props.logo}
                            alt='Gros'
                          />
                        </Link> 
                      </div>
                      <div className='header-action'>
                        <a href={str} className='header-phone'><Phone/>{props.phone}</a>
                        <Link to="/contact" className='btn btn-fill yellow'>{props.button}</Link>
                      </div>
                    </div>
                </div>
            </header>
    );      
    }
}

export default Header;