import React from "react";
import './Gallery.css';
import photos from '../../photos.js';

function Gallery(props) {
    function isHidden(el) {
        var style = window.getComputedStyle(el);
        return (style.display === 'none')
    }
    function showMorePhotos(e) {
        e.preventDefault();
        var list = document.getElementsByClassName('i-link');
        var i = 0,
            z = 8;
        if (window.innerWidth < 1199) {
            z = 6;
        }
        for (let index = 0; index < list.length; index++) {
            const element = list[index];
            if(i < z && isHidden(element)){
                element.style.display = 'block';
                i++;
            }
        }
    }
    return(
        <div className="section section-Gallery">
            <div className="container">
                <div className="section-title">{props.title}</div>
                <div className="gallery-wrap">
                    <div id="listPhotos">
                    {photos.map((photo, index) => (
                        
                            <a
                                href={photo.url}
                                className='i-link'
                                key={index}
                            >
                                <img
                                    className='i-image'
                                    src={photo.url}
                                    alt={photo.title}
                                />
                            </a>
                        ))}
                    </div>
                    <button className="btn btn-fill show-more" onClick={showMorePhotos}>Показать еще</button>
                </div>
            </div>
        </div>
    );
}
export default Gallery;