import React,{Component} from "react";
import Slider from "react-slick";
import Slide from "./slide";
import slides from "../../slides";
import "slick-carousel/slick/slick.css"; 
import "slick-carousel/slick/slick-theme.css";

export default class PreviousNextMethods extends Component {
    constructor(props) {
      super(props);
      this.next = this.next.bind(this);
      this.previous = this.previous.bind(this);
    }
    next() {
      this.slider.slickNext();
    }
    previous() {
      this.slider.slickPrev();
    }
    render() {
      const settings = {
        dots: false,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        arrows:false,
        fade:true
      };
      return (
        <div className="slider-wrap">
                <Slider  ref={c => (this.slider = c)} {...settings}>
                    {slides.map(slide => (
                        <Slide key={slide.id} slide={slide} />
                    ))}
                </Slider>
                <div className="sl-nav">
                    <button className="button" onClick={this.previous}>
                    <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg"><rect width="24" height="24" transform="matrix(-1 0 0 1 24 0)" fill="white"/><path opacity="0.2" d="M6.5 11.134C5.83333 11.5189 5.83333 12.4811 6.5 12.866L14 17.1962C14.6667 17.5811 15.5 17.0999 15.5 16.3301V7.66989C15.5 6.90009 14.6667 6.41896 14 6.80386L6.5 11.134Z" fill="black"/></svg>
                    </button>
                    <button className="button" onClick={this.next}>
                    <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg"><rect width="24" height="24" fill="white"/><path opacity="0.2" d="M17.5 11.134C18.1667 11.5189 18.1667 12.4811 17.5 12.866L10 17.1962C9.33333 17.5811 8.5 17.0999 8.5 16.3301V7.66989C8.5 6.90009 9.33333 6.41896 10 6.80386L17.5 11.134Z" fill="black"/></svg>
                    </button>                 
                </div>
        </div>
      );
    }
  }