import React from "react";
import {ReactComponent as Times} from '../../img/times2.svg';
import './modal.css';



function ModalWindow(props) {
    
    function closeModal(){
        document.body.style.overflow = 'auto'; 
        for (let el of document.querySelectorAll('.modal')) el.style.display = 'none';
    }

    function checkPos(params) {
        const elScr = document.getElementById(props.id);
        if(elScr.scrollTop > 0){
            elScr.classList.add('scrolled')
        }else{
            elScr.classList.remove('scrolled')
        }
    }

    return(
        <div className="modal" id={props.id} onScroll={checkPos}>
        <div className="modal-sandbox" onClick={closeModal}></div>
        <div className="modal-box">
            <div className="modal-header">
                <div className="modal-title">{props.title}</div>
                <div className="close-modal" onClick={closeModal}><Times/></div>
            </div>
            <div className="modal-body">
                {props.children}
            </div>
        </div>
        </div>
    )
}
export default ModalWindow;