import React, { useEffect, useRef, useState } from "react";
import { useNavigate } from "react-router-dom";
import ReCAPTCHA from "react-google-recaptcha";
import DatePicker from "react-datepicker";
import ru from 'date-fns/locale/ru';
import "react-datepicker/dist/react-datepicker.css";
import jobs from '../../jobCards';
import "./nice-select.css";
import $ from "jquery";
import InputMask from 'react-input-mask';
import { FileUploader } from "react-drag-drop-files";
window.jQuery = window.$ = $;
require("jquery-nice-select");


const recaptchaRef = React.createRef();

function ContactForm (){

    const selectRef = useRef();
    const [startDate, setStartDate] = useState('');
    useEffect(() => {

      $(selectRef.current).niceSelect();

    }, []);
  
    const [file, setFile] = useState(null);
    const handleChange = (file) => {
        setFile(file);
    };

    const [contactInfo, setContactInfo] = useState({
        name: "",
        email: "",
        phonenumber: "",
        birth_date: "",
        resume_link: "",
        captcha: false
    });
    const handleChangeSecond = (event) => {
        setContactInfo({ ...contactInfo, [event.target.name]: event.target.value });
        event.target.classList.remove('invalid')
    };
    const handleChangeDate = (date) => {
        setStartDate(date);
        const dateStr = new Date(date);
        var month = parseInt(dateStr.getMonth()) + 1;
        if(month < 10){
            month = '0'+month;
        }
        const str = dateStr.getDate() + '.' + month + '.' + dateStr.getFullYear();
        setContactInfo({ ...contactInfo, birth_date: str });
    }

    var succes = true;
    function onChange(value) {
        // console.log("Captcha value:", value);
        if(value){
            setContactInfo({ ...contactInfo, captcha: true });
        }
    }
    let navigate = useNavigate();
    const submitHandler = (e) => {
      e.preventDefault();
        const name = contactInfo.name;
        
        if(!contactInfo.captcha){
            succes = false;
        }
        if(name.length < 2){
            e.target.name.classList.add('invalid');
            e.target.name.nextSibling.style.display = 'block';
            succes = false;
        }else{
            e.target.name.classList.remove('invalid');
            e.target.name.nextSibling.style.display = 'none';
            e.target.name.previousSibling.classList.add('valid');
        }
        const phone = contactInfo.phonenumber.replace(/\D/g,'');
        if(phone.length === 0){
            e.target.phonenumber.classList.add('invalid');
            e.target.phonenumber.nextSibling.style.display = 'block'; 
            e.target.phonenumber.nextSibling.innerHTML = 'заполните поле';
            succes = false;
        }
        if(phone.length > 0 && phone.length < 11){
            e.target.phonenumber.classList.add('invalid');
            e.target.phonenumber.nextSibling.style.display = 'block'; 
            e.target.phonenumber.nextSibling.innerHTML = 'поле не заполнено до конца';
            succes = false;
        }
        if(phone.length === 11){
            e.target.phonenumber.classList.remove('invalid');
            e.target.phonenumber.nextSibling.style.display = 'none'; 
            e.target.phonenumber.previousSibling.classList.add('valid');
        }

        const dateBirth = contactInfo.birth_date.replace(/\D/g,'');
        if(dateBirth.length === 0){
            e.target.birth_date.classList.add('invalid');
            e.target.birth_date.parentNode.parentNode.nextSibling.style.display = 'block'; 
            e.target.birth_date.parentNode.parentNode.nextSibling.innerHTML = 'заполните поле';
            succes = false;
        }
        if(dateBirth.length > 0 && dateBirth.length < 8){
            e.target.birth_date.classList.add('invalid');
            e.target.birth_date.parentNode.parentNode.nextSibling.style.display = 'block'; 
            e.target.birth_date.parentNode.parentNode.nextSibling.innerHTML = 'поле не заполнено до конца';
            succes = false;
        }
        if(dateBirth.length === 8){
            e.target.birth_date.classList.remove('invalid');
            e.target.birth_date.parentNode.parentNode.nextSibling.style.display = 'none'; 
            e.target.birth_date.parentNode.parentNode.previousSibling.classList.add('valid');
        }

        const email = contactInfo.email;
        if(!/^[a-zA-Z0-9]+@[a-zA-Z0-9]+\.[A-Za-z]+$/.test(email) && email.length > 0){
            e.target.email.classList.add('invalid');
            e.target.email.nextSibling.style.display = 'block'; 
            e.target.email.nextSibling.innerHTML = 'поле заполнено не корректно';
            succes = false;
        }else{
            e.target.email.nextSibling.style.display = 'none';
        }
        if(/^[a-zA-Z0-9]+@[a-zA-Z0-9]+\.[A-Za-z]+$/.test(email) && email.length > 0){
            e.target.email.nextSibling.style.display = 'none';
            e.target.email.previousSibling.classList.add('valid'); 
        }


        if(succes){
            setContactInfo({ 
                name: "", 
                email: "", 
                phonenumber: "",
                birth_date: "",
                resume_link: "", 
            });
            navigate("../thankyou", { replace: true });
        }
    };

        return (
            <form className='contact-form' onSubmit={submitHandler}>
                <div className="form-cell">
                    <div className="input-label">Вакансия *</div>
                    <select name="vacancy" ref={selectRef} className="wide">
                    {jobs.map(card => (
                            <option key={card.id} value={card.title}>{card.title}</option>
                    ))}
                    </select>
                </div>
                <div className="form-cell">
                    <div className="input-label">ФИО *</div>
                    <input
                        name="name"
                        placeholder='Ваше имя'
                        value={contactInfo.name}
                        onChange={handleChangeSecond}
                    />
                    <div className='error'>заполните поле</div>
                </div>
                <div className="form-cell half">
                    <div className="input-label">Дата рождения *</div>
                    <DatePicker
                        name="birth_date"
                        placeholderText="28.07.2002" 
                        locale={ru}
                        selected={startDate}
                        onChange={handleChangeDate}
                        peekNextMonth
                        showMonthDropdown
                        showYearDropdown
                        dateFormat="dd.MM.yyyy"
                        dropdownMode="select"
                    />
                    <div className='error'>заполните поле</div>
                </div>
                <div className="form-cell half">
                    <div className="input-label">Пол</div>
                    <div className="radio-wrap">
                        <label><input type="radio" name="sex" value='мужской' defaultChecked/><span>мужской</span></label>
                        <label><input type="radio" name="sex" value='женский'/><span>женский</span></label>
                    </div>
                </div>
                <div className="form-cell half">
                    <div className="input-label">Контактый телефон *</div>
                    <InputMask 
                        name="phonenumber" 
                        mask="+7(999)999-99-99" 
                        maskChar="_" 
                        placeholder="+7 (" 
                        value={contactInfo.phonenumber}
                        onChange={handleChangeSecond}
                    />
                    <div className='error'>заполните поле</div>
                </div>
                <div className="form-cell half">
                    <div className="input-label">Электронная почта</div>
                    <input
                        name="email"
                        placeholder='Email для связи'
                        value={contactInfo.email}
                        onChange={handleChangeSecond}
                    />
                    <div className='error'>заполните поле</div>
                </div>
                <div className="form-cell">
                    <div className="input-label">Резюме</div>
                    <textarea
                        rows='10'
                        name="resume_link"
                        placeholder='Вставьте ссылку на резюме или прикрепите файл ниже'
                        value={contactInfo.resume_link}
                        onChange={handleChangeSecond}
                    ></textarea>
                    <div className={"uploader-wrap " + (file ? 'chosed':'')}>
                        <FileUploader name="file" label="выберите или перетащите файл" classes="drop-area" maxSize="1" handleChange={handleChange}/>
                        {file && <p>Выбрано: {file.name}</p>}    
                    </div>
                </div>
                <div className="form-cell half captcha">
                    <div className="input-label">Капча</div>
                    <ReCAPTCHA
                        size="normal"
                        ref={recaptchaRef}
                        sitekey="6LcZ3b4fAAAAAEXoSwJ71z11bqsxAiFmVFrtqcy9"
                        onChange={onChange}
                    />
                </div>
                <div className="form-cell half note">
                    * поля для обязательного заполнения
                </div>
                <div className="form-cell">
                    <div className="acceptance">
                        <label htmlFor="accept">
                            <input type="checkbox" id="accept" name="acceptance" required defaultChecked/>
                            <span>я подтверждаю согласие на обработку персональных данных и принимаю условия рассмотрения обращений *</span>
                        </label>
                    </div>
                </div>
                <button className='btn btn-fill' type='submit'>Отправить</button>
            </form>
        );
        
}
export default ContactForm;