import React from 'react';
import './footer.css';

function Footer(props) {
    
    const showModal = event => {
        console.log(event.target.dataset.modal);
        const linkAttr = event.target.dataset.modal;
        const modalW = document.getElementById(linkAttr);
        modalW.style.display = 'block';
        document.body.style.overflow = 'hidden'; 
    }
    return (
            <footer className='footer'>
                <div className='container'>
                    <div className='footer-top'>
                        <div className='d-flex j-between a-center'>
                            <div className='footer-brand'>
                                <img 
                                src={props.logo}
                                alt='Gros'
                                />
                            </div>
                            <div className="footer-social">
                                <p>Поделиться</p>
                                <ul>
                                    <li><a href="#link"><svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M12 0C5.373 0 0 5.373 0 12C0 18.627 5.373 24 12 24C18.627 24 24 18.627 24 12C24 5.373 18.627 0 12 0ZM15 8H13.65C13.112 8 13 8.221 13 8.778V10H15L14.791 12H13V19H10V12H8V10H10V7.692C10 5.923 10.931 5 13.029 5H15V8Z" fill="black"/></svg></a></li>
                                    <li><a href="#link"><svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M12 0C5.373 0 0 5.373 0 12C0 18.627 5.373 24 12 24C18.627 24 24 18.627 24 12C24 5.373 18.627 0 12 0ZM18.344 16.163H16.477C15.422 16.163 15.245 15.562 14.375 14.694C13.59 13.909 13.155 14.511 13.173 15.629C13.179 15.926 13.032 16.163 12.678 16.163C11.573 16.163 9.984 16.319 8.374 14.583C6.727 12.804 5 9.235 5 8.884C5 8.676 5.172 8.583 5.459 8.583H7.357C7.86 8.583 7.902 8.832 8.043 9.151C8.627 10.482 10.024 13.153 10.397 11.662C10.611 10.806 10.698 8.823 9.782 8.652C9.262 8.556 10.178 8 11.504 8C11.834 8 12.192 8.035 12.558 8.12C13.231 8.276 13.234 8.578 13.224 9.018C13.19 10.684 12.989 11.804 13.428 12.087C13.847 12.358 14.949 10.585 15.532 9.216C15.691 8.838 15.723 8.584 16.175 8.584H18.497C19.713 8.584 18.338 10.332 17.287 11.696C16.44 12.795 16.485 12.816 17.47 13.73C18.171 14.381 19 15.27 19 15.773C19 16.011 18.814 16.163 18.344 16.163Z" fill="black"/></svg></a></li>
                                </ul>
                            </div>
                        </div>    
                    </div>
                    <div className="footer-copyright d-flex j-between">
                        <div className="col">
                            <p>© Гросс маркет 2020</p>
                        </div>
                        <div className="col">
                            <a href="#modal" data-modal='modalPrivacy' onClick={showModal}>Политика обработки персональных данных</a>
                        </div>
                    </div>
                </div>
            </footer>
    );
}

export default Footer;